import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CardComponent} from './pages/card/card.component';
import {LoginComponent} from './pages/login/login.component';
import {SignupComponent} from './pages/signup/signup.component';
import {DetailComponent} from './pages/product-detail/detail.component';
import {CartComponent} from './pages/cart/cart.component';
import {AuthGuard} from "./_guards/auth.guard";
import {OrderComponent} from "./pages/order/order.component";
import {OrderDetailComponent} from "./pages/order-detail/order-detail.component";
import {ProductListComponent} from "./pages/product-list/product.list.component";
import {UserDetailComponent} from "./pages/user-edit/user-detail.component";
import {ProductEditComponent} from "./pages/product-edit/product-edit.component";
import {Role} from "./enum/Role";
import { HomepageComponent } from './homepage/homepage.component';
import{Checkout1Component} from "./pages/checkout1/checkout1.component";
import{Checkout2Component} from "./pages/checkout2/checkout2.component";
import{Checkout3Component} from "./pages/checkout3/checkout3.component";
import{Checkout4Component} from "./pages/checkout4/checkout4.component";
import{CustmeraccoutComponent} from "./pages/custmeraccout/custmeraccout.component";

    import { from } from 'rxjs';
const routes: Routes = [
    {path: 'home',component:HomepageComponent},
    {path: '', redirectTo: '/home', pathMatch: 'full'},

   
    //{path: '', redirectTo: '/home', pathMatch: 'full'},
    {path: 'productdetail', component: DetailComponent},
    {path: 'category/:id', component: CardComponent},
    {path: 'product', component: CardComponent},
    {path: 'category', component: CardComponent},
    {path: 'login', component: LoginComponent},
    {path: 'logout', component: LoginComponent},
    {path: 'register', component: SignupComponent},
    {path: 'cart', component: CartComponent},
    {path: 'success', component: SignupComponent},
    {path: 'wishlist', component: OrderDetailComponent, 
//    canActivate: [AuthGuard]
},
{path:'custaccout', component: CustmeraccoutComponent},
    {path:'customerdashboard', component: OrderComponent},
    {path: 'seller', redirectTo: 'seller/product', pathMatch: 'full'},
    {
        path: 'seller/product',
        component: ProductListComponent,
        //canActivate: [AuthGuard],
        data: {roles: [Role.Manager, Role.Employee]}
    },
    {
        path: 'profile',
        component: UserDetailComponent,
        //canActivate: [AuthGuard]
    },
    {
        path: 'seller/product/edit',
        component: ProductEditComponent,
        canActivate: [AuthGuard],
        data: {roles: [Role.Manager, Role.Employee]}
    },
    {
        path: 'seller/product/new',
        component: ProductEditComponent,
        //canActivate: [AuthGuard],
        data: {roles: [Role.Employee]}
    },
    {
        path:'checkout1', component: Checkout1Component
    },
    {
        path:'checkout2', component: Checkout2Component
    },
    {
        path:'checkout3', component: Checkout3Component
    },
    {
        path:'checkout4', component: Checkout4Component
    }

];

@NgModule({
    declarations: [],
    imports: [
        RouterModule.forRoot(routes)//{onSameUrlNavigation: 'reload'}
    ],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
